  
import { Typography } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import React from 'react';
// import DocumentCard from './Home_Components/Document_Cards/Document_Cards';
// import VideoGridList from './Home_Components/Document_Videos/Document_Videos';

function AboutMe() {
  return (
     
    <Container style={{ marginTop: 112 }}>
      <Grid container spacing={5}>
        {/* Videos section */}
        <Grid item xs={12}>
          <Grid container spacing={3} direction="column">
            {/* Header: Video */}
            <Grid item xs={12}>
              <Typography variant="h4" component="h4" className="heading">
                Videos
              </Typography>
            </Grid>
            {/* Video scroll section */}
            <Grid item xs={12}>
              {/* <VideoGridList /> */}
            </Grid>
          </Grid>
        </Grid>
        {/* Documents section */}
        <Grid item xs={12}>
          <Grid container spacing={3}>
            {/* Header: Documentations */}
            <Grid item xs={12}>
              <Typography variant="h4" component="h4" className="heading">
                Documentations
              </Typography>
            </Grid>
            {/* Document Cards Section */}
            <Grid item xs={12}>
              {/* <DocumentCard></DocumentCard> */}
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Container>
  );
}

export default AboutMe;